//
// Variables
//

// Colors
// fusv-disable
$white:                   #fff !default;
$bright:                  #c6c6c6 !default;
$gray-10:                 #f8f9fa !default;
$gray-20:                 #e9ecef !default;
$gray-30:                 #dee2e6 !default;
$gray-40:                 #ced4da !default;
$gray-50:                 #adb5bd !default;
$gray-60:                 #6c757d !default;
$gray-70:                 #495057 !default;
$gray-80:                 #343a40 !default;
$gray-90:                 #212529 !default;
$dark:                    #282828 !default;
$black:                   #000 !default;

$blue:                    #6897bb !default;

$primary:                 $blue !default;
// fusv-enable

// Options
//$enable-rfs:             true !default;
$enable-rounded:          true !default;

// Gradient
$gradient:                linear-gradient(180deg, rgba($white, .15), rgba($white, 0)) !default;

// Spacing
$spacer:                  1rem !default;

// Body
$body-bg:                 $dark !default;
$body-color:              $bright !default;
$body-text-align:         null !default;

// Links
$link-color:              $primary !default;
$link-decoration:         underline !default;
$link-hover-color:        lighten($link-color, 15%) !default;
$link-hover-decoration:   null !default;

// Paragraphs
$paragraph-margin-bottom: 1rem !default;

// Breakpoints

// Components
$border-width:            1px !default;

$border-radius:           .25rem !default;
$border-radius-sm:        .2rem !default;
//$border-radius-lg:        .3rem !default;

// Typography
// stylelint-disable value-keyword-case
$font-family-sans-serif:  system-ui, -apple-system, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji" !default;
$font-family-monospace:   "JetBrains Mono", Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace !default;
// stylelint-enable value-keyword-case
$font-family-base:        var(--murk-font-sans-serif) !default;
$font-family-code:        var(--murk-font-monospace) !default;

$font-size-root:          null !default;
$font-size-base:          1rem !default;
//$font-size-sm:           $font-size-base * .875 !default;
//$font-size-lg:           $font-size-base * 1.25 !default;

$font-weight-normal:      400 !default;
$font-weight-bold:        700 !default;
$font-weight-bolder:      bolder !default;

$font-weight-base:        $font-weight-normal !default;

$line-height-base:        1.5 !default;

$h1-font-size:            $font-size-base * 2.5 !default;
$h2-font-size:            $font-size-base * 2 !default;
$h3-font-size:            $font-size-base * 1.75 !default;
$h4-font-size:            $font-size-base * 1.5 !default;
$h5-font-size:            $font-size-base * 1.25 !default;
$h6-font-size:            $font-size-base !default;

$headings-margin-bottom:  $spacer / 2 !default;
$headings-font-family:    null !default;
$headings-font-style:     null !default;
$headings-font-weight:    500 !default;
$headings-line-height:    1.2 !default;
$headings-color:          null !default;

$small-font-size:         .85em !default;
$sub-sup-font-size:       .75em !default;

$hr-color:                inherit !default;
$hr-height:               $border-width !default;
$hr-opacity:              .25 !default;
$hr-margin-y:             $spacer !default;

$mark-padding:            .2em !default;
$mark-bg:                 #616146 !default;

$dt-font-weight:          $font-weight-bold !default;

// Code
$code-font-size:          $small-font-size !default;
$code-color:              $blue !default;
$pre-color:               null !default;

$kbd-padding-y:           .2rem !default;
$kbd-padding-x:           .4rem !default;
$kbd-font-size:           $code-font-size !default;
$kbd-color:               $dark !default;
$kbd-bg:                  $bright !default;

$nested-kbd-font-weight:  $font-weight-bold !default;
